.announcement {
  width: 100%;
  height: auto;
  margin: 0 auto;
  padding: 15px 0;
  background: #ebebeb;
  display: block;
}

.announcement>div {
  display: flex;
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
}

.announcement marquee {
  font-size: 14px;
  font-weight: 500;
  margin: 0 15px;
  color: #000000;
}
.custom_links{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.custom_links a{
  border-radius: 0 !important;
  font-size: 18px;
  color: #ffffff;
  background: #236ba0;
  padding: 15px 0;
  border: none !important;
  width: 50%;
  text-align: center;
}
.custom_links .register{
  background-color: #cc001b;
}
.custom_tabs_card{
  border-bottom: 1px solid #ebebeb !important;
}
.custom_tabs_card img{
  padding: 20px;
}
.custom_tabs_card p{
  width: 100%;
  height: auto;
  margin: 0 auto;
  text-align: center;
  font-size: 0.8rem;
  font-weight: 600;
  color: #343232 !important;
  padding-bottom: 10px;
  display: block;
}
.copy_rights{
  width: 100%;
  color: #236ba0;
    font-weight: 600;
    font-size: 0.8rem;
    padding-bottom: 60px;
    background: #ebebeb;
}
.custom_title{
  display: flex;
    flex-direction: column;
}
.custom_title div{
  border-bottom: 1px solid #104871;
    background: #236ba0;
}
.custom_title img{
 width: 100px;
 height: auto;
 padding: 10px 0;
}
.contact_img img{
  width: 100%;
  max-width: 50px;
  height: auto;
  text-align: center;
  margin: 0 auto;
  padding: 10px 5px;
}
.contact_img p{
  font-size: 15px !important;
  color: #ffffff !important;
  font-weight: 600 !important;
  padding: 19px 5px;
  text-align: left;
}
.nav_deposit a{
  font-size: 15px;
  color: #ffffff;
  font-weight: 400;
  text-align: center;
  margin: 0 auto;
  display: block;
  text-transform: uppercase;
}
.nav_deposit .depo{
  border-right: 1px solid #104871;
}
.nav_deposit{
  padding: 15px 0;
}